@import "../utils/_mixins";
@import "../utils/_variables";

body {
   margin: 0px;
   padding: 0px;
   @include font($base-fz, $base-lh, $base-fc);
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   background-color: $base-bg;
   height: 100%;
   position: relative;
}

/*MAIN*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
   margin: 0;
   padding: 0;
   border: 0;
   font-size: 100%;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
   display: block;
}
blockquote,
q {
   quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
   content: "";
   content: none;
}
table {
   border-collapse: collapse;
   border-width: 0px;
   padding: 0px;
   margin: 0px;
}
html {
   height: 100%;
   -webkit-text-size-adjust: none;
   -ms-text-size-adjust: none;
}
input,
textarea {
   color: #333;
   font-family: $base-ff;
   outline: none;
   border-radius: 0;
   -webkit-appearance: none;
}
input[type="button"],
input[type="submit"],
button {
   cursor: pointer;
}
td {
   margin: 0px;
   padding: 0px;
}
form {
   padding: 0px;
   margin: 0px;
}
a {
   color: $link-color;
   transition: $default-transition;
   text-decoration: none;
   outline: none;
   -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
a,
span,
div,
button {
   outline: none !important;
}
input[type="submit"],
input[type="button"],
button {
   -webkit-appearance: none;
   outline: none;
}
* {
   box-sizing: border-box;
}
*:before,
*:after {
   box-sizing: border-box;
}
.clearfix:after,
.wrapper:after,
.row:after {
   content: ".";
   display: block;
   height: 0;
   clear: both;
   visibility: hidden;
}
